/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  /* position: absolute; */
  /* bottom: -25px; */
  display: block;
  /* width: 100%; */
  padding: 0;
  margin: 0 auto;
  list-style: none;
  text-align: center;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  width: 1.5rem;
  height: 16px;
  margin: 0 2px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  padding: 0;
  border: 0;
  background: 0;
  border-bottom: 1px solid rgb(116, 118, 118);
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.slick-dots li.slick-active button {
  box-shadow: 0px -5px 0px -1px #000000 inset;
  border-bottom: 0;
}

.slick-dots button:focus {
  box-shadow: 0px -5px 0px -1px rgb(116, 118, 118) inset;
  border-bottom: 0;
}

.remove_margin .slick-track {
  margin: 0;
}

